.mobile-menu {
  display: none;

  @media screen and (max-width: @small) {
    display: block;
    width: 100%;
    height: 62px;
    background-color: @dark-bg;
    position: fixed;
    left: 0;
    bottom: 0;
    z-index: 20;
  }
}

.mobile-menu__list {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 22px;
}

.mobile-menu__link {
  padding-top: 10px;
  padding-bottom: 11px;
  display: flex;
  justify-content: space-between;
  flex-direction: column-reverse;
  align-items: center;
  color: #ADBAD4;
}

.mobile-menu__link--more {
  padding-top: 20px;

  img {
    margin-bottom: 10px;
  }
}
