.slider--main {
  .slider__wrapper {
    height: 100%;
    background-color: #124239;

    @media screen and (max-width: @small) {
      background-color: transparent;
    }

    .slick-dots {
      position: absolute;
      top: 53px;
      left: 50px;
      display: flex;

      @media screen and (max-width: @medium) {
        top: 40px;
        left: 30px;
      }

      @media screen and (max-width: @small) {
        left: 20px;
        top: 15px;
      }

      li {
        width: 60px;
        height: 3px;
        background-color: rgba(255, 255, 255, 0.2);
        margin-right: 8px;
        position: relative;

        @media screen and (max-width: @small) {
          width: 26px;
        }

        button {
          padding: 0;
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 3px;
          background-color: rgba(255, 255, 255, 0.2);
          font-size: 0;
          border: 0;
        }
      }

      .slick-active {
        button {
          padding: 0;
          background-color: rgba(255, 255, 255, 1);
          animation: widthButton 4s;
        }
      }

      @keyframes widthButton {
        0% {
          width: 0px;
        }

        100% {
          width: 100%;
        }
      }
    }
  }

  .slider__item {
    height: 100%;
    background-image: url("../img/slider-item-bg.jpg");
    background-size: 895px 495px;
    background-position: 50%;
    background-repeat: no-repeat;

    @media screen and (max-width: @extra-large) {
      background-size: 100% 495px;
    }

    @media screen and (max-width: @small) {
      background-size: 100% 74%;
      background-position: 0 0;
    }
  }

  .slider__item-wrapper {
    height: 100%;
    padding-top: 176px;
    padding-left: 50px;
    padding-bottom: 87px;
    position: relative;

    @media screen and (max-width: @medium) {
      padding-top: 92px;
      padding-left: 30px;
      padding-bottom: 50px;
    }

    @media screen and (max-width: @small) {
      padding-top: 68px;
      padding-left: 18px;
      padding-bottom: 0;
    }
  }

  .slider__item-title {
    font-size: 40px;
    line-height: 1;
    margin-bottom: 27px;
    font-family: @mont;
    font-weight: 700;
    color: @white;
    width: 100%;
    max-width: 462px;
    margin-top: 0;

    @media screen and (max-width: @medium) {
      position: relative;
      z-index: 2;
    }

    @media screen and (max-width: @small) {
      font-size: 23px;
      line-height: 1;
      padding-right: 30px;
      margin-bottom: 12px;
    }
  }

  .slider__articul {
    font-size: 25px;
    line-height: 1;
    font-family: @mont;
    font-weight: 400;
    margin-bottom: 37px;
    color: @white;

    @media screen and (max-width: @medium) {
      margin-bottom: 70px;
    }

    @media screen and (max-width: @small) {
      font-size: 13px;
      line-height: 1;
      margin-bottom: 30px;
    }
  }

  .slider__item-link {
    font-size: 17px;
    line-height: 21px;
    font-family: @mont;
    font-weight: 600;
    color: @white;
    display: inline-block;
    padding-right: 20px;
    position: relative;

    &::after {
      content: "";
      display: block;
      width: 7px;
      height: 7px;
      position: absolute;
      top: 8px;
      right: 5px;
      background-image: url(../img/svg/triangle-white.svg);
      background-size: 7px 7px;
      background-position: 50%;
      background-repeat: no-repeat;
      transition: all ease-in 0.2s;

      @media screen and (max-width: @small) {
        display: none;
      }
    }

    @media screen and (max-width: @small) {
      display: block;
      background-color: @blue;
      width: calc(100% + 18px);
      margin-left: -18px;
      text-align: center;
      padding: 16.5px 0;
      font-size: 13px;
      line-height: 16px;
    }
  }

  .slider__img-wrapper {
    width: 355px;
    position: absolute;
    top: 0;
    right: 0;
    text-align: center;
    padding-right: 35px;
    z-index: 0;

    @media screen and (max-width: @medium) {
      width: 275px;
    }

    @media screen and (max-width: @small) {
      width: 132px;
    }
  }

  .slider__img {
    @media screen and (max-width: @medium) {
      width: 240px;
      height: 368px;
    }

    @media screen and (max-width: @small) {
      width: 107px;
      height: 165px;
    }
  }
}

.slider--partners {
  .slick-track {
    padding-bottom: 40px;
  }

  .slider__wrapper {
    background-color: @white;
    min-height: 218px !important;

    @media screen and (max-width: @small) {
      min-height: 108px !important;
    }
  }

  .slider__item {
    min-height: 109px;
    display: flex;
    position: relative;
    width: 100%;
    // max-width: 590px;
    background-image: none;
    background-color: #F4F7FB;
    padding-top: 28.5px;
    padding-bottom: 28.5px;
    padding-left: 44px;
    padding-right: 240px;

    @media screen and (max-width: @medium) {
      max-width: 100% !important;
      padding-left: 118px;
    }

    @media screen and (max-width: @small) {
      padding-top: 8.5px;
      padding-bottom: 8.5px;
      padding-left: 22px;
      padding-right: 118px;
      min-height: 49px;
    }
  }

  .slick-active {
    margin-right: 20px;

    @media screen and (max-width: @medium) {
      margin-right: 0;
    }
  }

  .slider__bold {
    width: 100%;
    max-width: 280px;
    font-family: @mont;
    font-size: 20px;
    line-height: 26px;
    color: @black;

    @media screen and (max-width: @small) {
      font-size: 13px;
      line-height: 17px;
    }
  }

  .slider__img {
    position: absolute;
    top: 0;
    right: 60px;

    @media screen and (max-width: @medium) {
      right: 98px;
    }

    @media screen and (max-width: @small) {
      right: -20px;
      top: -37px;
      transform: scale(0.6);
    }
  }
}
