.products {
  border: 1px solid #DCDDE6;

  .slick-arrow {
    width: 53px;
    height: 53px;
    border-radius: 50%;
    box-shadow: 0px 3px 10px 0px rgba(0, 0, 0, 0.1);
    position: absolute;
    top: 41%;
    transform: translateY(50%);
    border: none;
    background-color: @white;
    z-index: 1;
    outline: none;

    @media screen and (max-width: @medium) {
      display: none;
    }
  }

  .slick-prev {
    font-size: 0;
    transform: translateX(-55%);

    &::after {
      content: "";
      display: block;
      width: 15px;
      height: 15px;
      position: absolute;
      top: 50%;
      left: 50%;
      margin-top: -7.5px;
      margin-left: -7.5px;
      background-image: url("../img/svg/blue-triangle-icon.svg");
      background-size: 15px 15px;
      background-position: 0 0;
      background-repeat: no-repeat;
    }

    &:hover {
      &::after {
        background-image: url("../img/svg/red-triangle-icon.svg");
        transform: rotate(180deg);
      }
    }

    @media screen and (max-width: @medium) {
      display: none;
    }
  }

  .slick-next {
    font-size: 0;
    transform: translateX(55%);
    right: 0;

    &::after {
      content: "";
      display: block;
      width: 15px;
      height: 15px;
      position: absolute;
      top: 50%;
      left: 50%;
      margin-top: -7.5px;
      margin-left: -7.5px;
      background-image: url("../img/svg/blue-triangle-icon.svg");
      transform: rotate(180deg);
      background-size: 15px 15px;
      background-position: 0 0;
      background-repeat: no-repeat;
    }

    &:hover {
      &::after {
        background-image: url("../img/svg/red-triangle-icon.svg");
        transform: rotate(0);
      }
    }

    @media screen and (max-width: @medium) {
      display: none;
    }
  }
}

.products__item {
  display: flex !important;
  flex-direction: column;
  height: 100%;
  min-height: 627px;
  border-right: 1px solid #DCDDE6;
  transition: all ease 0.3s;
  position: relative;
  z-index: 1;

  @media screen and (max-width: @small) {
    min-height: 574px;
  }

  &:hover {
    position: relative;
    z-index: 2;
    box-shadow: 0px 10px 13px 0px rgba(15, 29, 55, 0.11);
  }
}

.products__item--new {
  .products__img-wrapper {
    position: relative;

    &::after {
      content: "Hовинка";
      font-size: 14px;
      line-height: 19px;
      background: #C95A60;
      color: @white;
      padding: 3px 10px;
      position: absolute;
      bottom: -11px;
      left: 20px;

      @media screen and (max-width: @small) {
        bottom: -34px;
      }
    }
  }
}

.products__img-wrapper {
  height: 255px;
  padding-left: 56px;
  padding-right: 56px;
  padding-top: 23px;
  padding-bottom: 45px;
  text-align: center;

  @media screen and (max-width: @extra-large) {
    padding-left: 26px;
    padding-right: 26px;
  }

  @media screen and (max-width: @small) {
    height: 234px;
  }
}

.products__img {
  @media screen and (max-width: @extra-large) {
    display: inline!important;
    width: 90%;
    height: auto;
    object-fit: cover;
  }

  @media screen and (max-width: @small) {
    width: 80%;
  }
}

.products__txt {
  padding-top: 32px;
  padding-left: 20px;
  padding-right: 20px;
  padding-bottom: 35px;
  background-color: @grey;
  flex-grow: 1;
  display: flex;
  flex-direction: column;

  @media screen and (max-width: @small) {
    padding-top: 53px;
    padding-bottom: 20px;
  }
}

.products__link {
  font-size: 20px;
  line-height: 24px;
  font-family: @mont;
  font-weight: 600;
  margin-bottom: 15px;
  color: @black;

  @media screen and (max-width: @small) {
    font-size: 18px;
  }
}

.products__dscr {
  line-height: 19px;

  @media screen and (max-width: @small) {
    font-size: 13px;
  }
}

.products__price {
  margin-top: auto;
  font-family: @mont;
  font-size: 30px;
  line-height: 30px;
  font-weight: 700;
  color: @black;
  margin-bottom: 25px;

  @media screen and (max-width: @small) {
    font-size: 20px;
    line-height: 21px;
    margin-bottom: 18px;
  }

  &::after {
    content: " \20BD";
  }
}

.products__buy {
  border: none;
}

.products__item--catalog {
  width: 33.3%;
}

.products--catalog {
  border: none;

  .products__item {
    border-top: 1px solid #dcdde6;
    border-bottom: 1px solid #dcdde6;

    &:nth-child(6n-2) {
      border-top: none;
    }

    &:nth-child(6n-1) {
      border-top: none;
    }

    &:nth-child(6n) {
      border-top: none;
    }

    &:nth-child(3n-2) {
      border-left: 1px solid #dcdde6;
    }
  }
}
