.novelty-products {
  margin-top: -105px;
  position: relative;
  z-index: 10;

  @media screen and (max-width: @medium) {
    margin-top: -75px;
    margin-bottom: 40px;
  }
}

.novelty-products__wrapper {
  overflow-x: hidden;
}

.novelty-products__title {
  font-size: 30px;
  line-height: 30px;
  font-family: @mont;
  color: @black;
  display: inline-block;
  position: relative;
  padding-right: 53px;

  @media screen and (max-width: @small) {
    font-size: 20px;
    line-height: 1;
  }

  &::after {
    content: "";
    width: 33px;
    height: 33px;
    display: block;
    background-image: url("../img/svg/new-icon.svg");
    background-size: 33px 33px;
    background-position: 0 0;
    background-repeat: no-repeat;
    position: absolute;
    top: 0;
    right: 0;
  }
}

@media screen and (max-width: @medium) {
  .novelty-products__list {
    width: 1200px;
  }
}
