.main-info {
  @media screen and (max-width: @medium) {
    margin-top: -55px;
  }
}

.main-info-wrapper {
  padding-top: 40px;
  padding-bottom: 60px;

  @media screen and (max-width: @small) {
    padding-bottom: 0;
  }
}

.main-info__title {
  font-size: 30px;
  line-height: 30px;
  font-family: @mont;
  color: @black;

  @media screen and (max-width: @small) {
    font-size: 20px;
    line-height: 1;
    margin-bottom: 23px;
  }
}

.main-info__content {
  display: flex;

  @media screen and (max-width: @medium) {
    flex-direction: column;
  }
}

.main-info__img-wrapper {
  padding-right: 52px;
  position: relative;

  @media screen and (max-width: @medium) {
    margin-bottom: 40px;
    padding-right: 0;
  }
}

.main-info__img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.main-info__bold-txt {
  font-size: 63px;
  line-height: 1;
  font-weight: 700;
  transform: rotate(270deg);
  background-color: transparent;
  display: block;
  position: absolute;
  bottom: 167px;
  right: -128px;
  color: @white;
  letter-spacing: 0.26rem;

  @media screen and (max-width: @medium) {
    font-size: 84px;
    line-height: 84px;
    bottom: 220px;
    right: -235px;
  }

  @media screen and (max-width: @small) {
    font-size: 37px;
    line-height: 1;
    right: -106px;
    bottom: 105px;
    letter-spacing: 0.15rem;
  }
}

.main-info__content-txt {
  justify-content: space-between;
  display: flex;
  flex-grow: 1;
  padding-right: 30px;
  margin-top: -8px;

  @media screen and (max-width: @small) {
    flex-direction: column;
    padding-right: 0;
  }

  p {
    width: 258px;

    @media screen and (max-width: @medium) {
      width: 47%;
    }

    @media screen and (max-width: @small) {
      width: 100%;
      margin-bottom: 30px;
    }
  }
}

.main-info__more {
  display: inline-block;
  padding-right: 20px;
  margin-top: 32px;
  color: #214FA8;
  font-family: @mont;
  font-weight: 600;
  font-size: 17px;
  line-height: 21px;
  position: relative;

  &::after {
    content: "";
    display: block;
    width: 7px;
    height: 7px;
    position: absolute;
    top: 9px;
    right: 5px;
    background-image: url(../img/svg/triangle-blue.svg);
    background-size: 7px 7px;
    background-position: 50%;
    background-repeat: no-repeat;
    transition: all ease-in 0.2s;
  }

  &:hover {
    &::after {
      right: 0;
    }
  }

  @media screen and (max-width: @medium) {
    margin-top: 85px;
    width: 100%;

    &::after {
      right: 178px;
    }

    &:hover {
      &::after {
        right: 175px;
      }
    }
  }

  @media screen and (max-width: @small) {
    margin-top: 32px;
    font-size: 13px;

    &::after {
      right: 64%;
    }
  }
}
