.footer {
  background-color: @dark-bg;
}

.footer__wrapper {
  padding-top: 50px;
  padding-bottom: 55px;
  display: flex;

  @media screen and (max-width: @large) {
    flex-wrap: wrap;
    padding-bottom: 0;
  }

  @media screen and (max-width: @small) {
    padding-top: 40px;
  }
}

.footer__column {
  margin-right: 150px;
  width: 100%;
  max-width: 240px;

  @media screen and (max-width: @small) {
    margin-right: 0;
    max-width: 100%;
  }
}

.footer__wrapper-contacts {
  @media screen and (max-width: @small) {
    width: 100%;
  }
}

.footer__tel {
  margin-bottom: 27px;

  @media screen and (max-width: @small) {
    margin-bottom: 20px;
  }
}

.footer__tel-span {
  display: block;
  font-size: 14px;
  line-height: 25px;
  color: @white;
  margin-bottom: 6px;

  @media screen and (max-width: @small) {
    font-size: 13px;
    line-height: 24px;
  }
}

.footer__tel-link {
  font-family: @mont;
  font-weight: 700;
  font-size: 25px;
  line-height: 25px;
  color: @white;

  @media screen and (max-width: @small) {
    font-size: 20px;
    line-height: 20px;
  }
}

.footer__mail {
  margin-bottom: 25px;
}

.footer__mail-span {
  display: block;
  font-size: 14px;
  line-height: 25px;
  color: @white;
  margin-bottom: 5px;

  @media screen and (max-width: @small) {
    font-size: 13px;
    line-height: 24px;
  }
}

.footer__mail-link {
  font-family: @mont;
  font-weight: 700;
  font-size: 25px;
  line-height: 25px;
  color: @white;

  @media screen and (max-width: @small) {
    font-size: 20px;
    line-height: 20px;
  }
}

.footer__btn {
  text-align: center;
  padding-top: 14px;
  padding-bottom: 14px;
  padding-left: 55px;
  padding-right: 55px;

  @media screen and (max-width: @small) {
    width: 100%;
  }
}

.footer__wrapper-menus {
  display: flex;
}

.footer__nav {
  margin-right: 85px;

  @media screen and (max-width: @small) {
    margin-right: 70px;
  }
}

.footer__nav,
.footer__menu {
  padding-top: 25px;

  @media screen and (max-width: @small) {
    padding-top: 62px;
  }
}

.footer__nav-item,
.footer__menu-item {
  margin-bottom: 32px;

  @media screen and (max-width: @medium) {
    &:last-child {
      margin-bottom: 62px;
    }
  }
}

.footer__nav-link,
.footer__menu-link {
  font-family: @mont;
  font-size: 14px;
  line-height: 1;
  color: @white;

  @media screen and (max-width: @small) {
    font-size: 13px;
  }
}

.footer__wrapper-info {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-end;
  flex-grow: 1;
  padding-top: 20px;
  padding-bottom: 30px;
  padding-right: 25px;

  @media screen and (max-width: @medium) {
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    border-top: 1px solid #465062;
  }

  @media screen and (max-width: @small) {
    padding-top: 25px;
    flex-direction: column;
  }
}

.footer__priv-pol {
  width: 100%;
  max-width: 260px;
  font-size: 14px;
  line-height: 14px;
  font-family: @mont;
  font-weight: 600;
  color: @white;

  @media screen and (max-width: @medium) {
    margin-right: 19%;
  }

  @media screen and (max-width: @small) {
    font-size: 13px;
    margin-right: 0;
    max-width: 100%;
    margin-bottom: 20px;
  }
}

.footer__priv-pol-info {
  width: 100%;
  max-width: 260px;

  @media screen and (max-width: @small) {
    max-width: 240px;
    font-size: 13px;
    margin-right: auto;
  }
}
