.main-slider {
  @media screen and (max-width: @medium) {
    margin-bottom: 0px;
  }

  @media screen and (max-width: @small) {
    margin-bottom: 0;
  }
}

.main-slider__wrapper {
  display: flex;
  padding-top: 20px;

  @media screen and (max-width: @small) {
    padding-top: 0;
  }
}

.main-slider__container {
  margin-left: auto;
  width: 100%;
  max-width: 895px;
  height: 495px;

  @media screen and (max-width: @extra-large) {
    max-width: 100%;
  }

  @media screen and (max-width: @medium) {
    height: 384px;
  }

  @media screen and (max-width: @medium) {
    height: auto;
  }
}
