.main-news {
  background-color: @grey;
  padding-top: 47px;
  padding-bottom: 70px;

  @media screen and (max-width: @medium) {
    padding-top: 24px;
    padding-bottom: 50px;
  }

  @media screen and (max-width: @small) {
    padding-top: 42px;
    padding-bottom: 40px;
  }
}

.main-news__list-wrapper {
  @media screen and (max-width: @large) {
    overflow-x: scroll;
    width: calc(100% + 40px);
    padding-left: -20px;
  }

  @media screen and (max-width: @small) {
    width: calc(100% + 23px);
  }
}

.main-news__title {
  font-size: 30px;
  line-height: 30px;
  font-family: @mont;
  color: @black;
  display: inline-block;
  position: relative;
  padding-right: 53px;

  &::after {
    content: "";
    width: 25px;
    height: 40px;
    display: block;
    background-image: url("../img/svg/fire-icon.svg");
    background-size: 25px 40px;
    background-position: 0 0;
    background-repeat: no-repeat;
    position: absolute;
    top: -5px;
    right: 0;
  }

  @media screen and (max-width: @small) {
    font-size: 20px;
    line-height: 1;
    margin-bottom: 40px;
  }
}

.main-news__list {
  display: flex;
  justify-content: space-between;
  margin-bottom: 15px;

  @media screen and (max-width: @medium) {
    width: 1200px;
    //margin-left: -20px;
    margin-bottom: 40px;
  }

  @media screen and (max-width: @small) {
    margin-bottom: 20px;
  }
}

.main-news__list-item {
  width: 100%;
  max-width: 285px;

  @media screen and (max-width: @medium) {
    margin-right: 20px;
  }
}

.main-news__img-wrapper {
  width: 100%;
  height: 224px;
  margin-bottom: 25px;
  position: relative;
}

.main-news__img {
  width: 100%;
  height: auto;
  object-fit: cover;
}

.main-news__date {
  display: inline-block;
  padding-left: 3px;
  padding-right: 3px;
  background-color: @white;
  font-weight: 500;
  font-size: 12px;
  line-height: 1;
  color: @black;
  position: absolute;
  bottom: 20px;
  left: 20px;
  z-index: 1;
  font-family: @mont;
}

.main-news__link {
  font-size: 20px;
  line-height: 24px;
  font-weight: 600;
  font-family: @mont;
  color: @black;
  display: block;
  margin-bottom: 20px;
  padding-right: 14px;

  @media screen and (max-width: @small) {
    font-size: 18px;
    margin-bottom: 10px;
  }

  &:hover {
    color: #214FA8;
  }
}

.main-news__link-all {
  color: #214FA8;
  font-size: 14px;
  line-height: 17px;
  font-weight: 600;
  font-family: @mont;
  display: inline-block;
  padding-right: 20px;
  position: relative;

  &::after {
    content: "";
    display: block;
    width: 7px;
    height: 7px;
    position: absolute;
    top: 6px;
    right: 5px;
    background-image: url(../img/svg/triangle-blue.svg);
    background-size: 7px 7px;
    background-position: 50%;
    background-repeat: no-repeat;
    transition: all ease-in 0.2s;
  }

  &:hover {
    &::after {
      right: 0;
    }
  }

  @media screen and (max-width: @medium) {
    display: inline-block;
    margin-top: -15px;
  }

  @media screen and (max-width: @small) {
    font-size: 13px;
  }
}
