.header__top {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  align-content: flex-start;
  min-height: 68px;

  @media screen and (max-width: @medium) {
    flex-wrap: wrap;
  }
}

.header__mudium-icon {
  display: none;

  @media screen and (max-width: @extra-large) {
    display: block;
  }
}

.header__logo {
  margin-right: 13.5%;
  margin-top: 6px;

  @media screen and (max-width: @extra-large) {
    padding-top: 10px;
    margin-right: 46px;
  }

  @media screen and (max-width: @small) {
    margin-right: 70px;
  }
}

.header__logo-img {
  @media screen and (max-width: @extra-large) {
    width: 122px;
    height: 40px;
  }

  @media screen and (max-width: @small) {
    width: 98px;
    height: 32px;
  }
}

.header__top-user {
  display: flex;
  min-height: 20px;
  margin-top: 22px;

  @media screen and (max-width: @extra-large) {
    margin-left: auto;
  }
}

.header__time {
  display: block;
  padding-top: 5px;
  padding-left: 30px;
  position: relative;
  margin-right: 43px;
  font-family: @mont;
  font-size: 14px;
  line-height: 17px;
  font-weight: 600;
  white-space: nowrap;

  @media screen and (max-width: @extra-large) {
    margin-right: 88px;
  }

  @media screen and (max-width: @extra-large) {
    margin-right: 88px;
  }

  @media screen and (max-width: @small) {
    display: none;
  }

  &::after {
    content: "";
    display: block;
    width: 20px;
    height: 20px;
    background-image: url("../img/clock-icon.png");
    background-position: 0 0;
    background-size: 20px 20px;
    background-repeat: no-repeat;
    position: absolute;
    top: 3px;
    left: 0;
  }
}

.header__tel {
  padding-right: 40px;

  @media screen and (max-width: @extra-large) {
    padding-right: 36px;
  }

  @media screen and (max-width: @small) {
    padding-right: 16px;
  }
}

.header__mail {
  padding-right: 40px;
  position: relative;

  @media screen and (max-width: @small) {
    padding-right: 16px;
  }

  &::after {
    content: "";
    display: block;
    width: 1px;
    height: 20px;
    position: absolute;
    top: 50%;
    right: 0;
    transform: translateY(-50%);
    background-color: #868794;

    @media screen and (max-width: @extra-large) {
      display: none;
    }
  }
}

.header__mail-link {
  font-family: @mont;
  font-size: 14px;
  line-height: 17px;
  font-weight: 600;
  color: @text-color;

  @media screen and (max-width: @extra-large) {
    display: block;
    font-size: 0;
    position: relative;
    width: 21px;

    .header__tel-mudium-icon {
      position: absolute;
      top: 3px;
      left: 0;
    }
  }

  @media screen and (max-width: @extra-large) {
    .header__tel-mudium-icon {
      position: absolute;
      top: 0;
      left: 0;
    }
  }
}

.header__search {
  padding-left: 35px;
  padding-right: 25px;
  height: 25px;
  position: relative;

  @media screen and (max-width: @extra-large) {
    height: 34px;
    padding-left: 0;
  }

  @media screen and (max-width: @small) {
    display: none;
  }

  &::after {
    content: "";
    display: block;
    width: 1px;
    height: 20px;
    position: absolute;
    top: 50%;
    right: 0;
    transform: translateY(-50%);
    background-color: #868794;

    @media screen and (max-width: @extra-large) {
      display: none;
    }
  }

  @media screen and (max-width: @extra-large) {
    .header__tel-mudium-icon {
      display: block;
      margin-top: 3px;
    }
  }
}

.header__search-form {
  width: 115px;
  position: relative;

  @media screen and (max-width: @extra-large) {
    display: none;
    position: absolute;
    bottom: -20px;
    border: 1px solid @black;
    left: -15px;
    padding: 5px;
    background-color: @white;
    border-radius: 5px;
    z-index: 2;
  }
}

.header__seacrh-input {
  width: 85px;
  padding-left: 25px;
  border: none;

  &::placeholder {
    font-family: @mont;
    font-size: 14px;
    line-height: 17px;
    font-weight: 600;
    color: @text-color;
  }
}

.header__search-submit {
  display: block;
  width: 25px;
  height: 25px;
  background-image: url("../img/search-icon.png");
  background-position: 0 0;
  background-size: 25px 25px;
  background-repeat: no-repeat;
  position: absolute;
  top: -3px;
  left: 0;
  border: none;
  padding: 0;
  background-color: transparent;

  @media screen and (max-width: @extra-large) {
    top: 5px;
  }
}

.header__cart {
  padding-left: 40px;
  width: 62px;
  height: 20px;
  position: relative;

  @media screen and (max-width: @extra-large) {
    height: 34px;
    padding-left: 15px;
    margin-top: 4px;
  }

  @media screen and (max-width: @small) {
    display: none;
  }
}

.header__cart-count {
  position: absolute;
  top: -5px;
  right: -8px;
  font-family: @mont;
  font-size: 11px;
  line-height: 13px;
  font-weight: 600;
  color: @white;
  background-color: @blue;
  padding: 2px 4px;
  border-radius: 50%;

  @media screen and (max-width: @extra-large) {
    right: 14px;
  }
}

.header__cart-link {
  display: block;
  width: 20px;
  height: 20px;
  background-image: url("../img/svg/cart-icon.svg");
  background-position: 0 0;
  background-size: 20px 20px;
  background-repeat: no-repeat;
}

.header__login {
  margin-left: auto;
  margin-top: 12px;
  margin-bottom: 12px;

  @media screen and (max-width: @medium) {
    margin-left: auto;
    margin-right: auto;
  }

  @media screen and (max-width: @small) {
    margin-bottom: 0;
  }
}

.header__login-btn {
  width: 112px;
  text-align: center;
  padding: 13px 0;

  @media screen and (max-width: @small) {
    width: 82px;
    padding: 7.5px 0;
    margin-top: 2px;
  }
}

.header__tel-link {
  font-family: @mont;
  font-size: 14px;
  line-height: 17px;
  font-weight: 600;
  color: @black;

  @media screen and (max-width: @extra-large) {
    display: block;
    font-size: 0;
    position: relative;
    width: 21px;

    .header__tel-mudium-icon {
      position: absolute;
      top: 5px;
      left: 0;
      width: 19px;
    }
  }

  @media screen and (max-width: @extra-large) {
    .header__tel-mudium-icon {
      top: 0;
    }
  }
}

.header__catalog-btn {
  position: relative;
  padding-left: 45px;
  padding-top: 14px;
  padding-bottom: 13px;
  padding-right: 116px;
  background: #263859;
  border: none;
  color: @white;
  font-family: @mont;
  font-size: 14px;
  line-height: 17px;
  font-weight: 600;
  outline: none;

  @media screen and (max-width: @extra-large) {
    padding-right: 25px;
  }

  @media screen and (max-width: @medium) {
    span {
      display: none;
    }
  }
}

.header__burger {
  display: block;
  height: 15px;
  width: 16px;
  position: absolute;
  background-color: transparent;
  border: none;
  outline: none;
  top: 17px;
  left: 20px;
  background-image: url(../img/burger-icon.png);
  background-size: cover;
  background-position: 50%;
  background-repeat: no-repeat;
}

.header__bottom {
  min-height: 44px;
  background-color: @dark-bg;

  @media screen and (max-width: @small) {
    display: none;
    position: fixed;
    width: 100%;
    height: 100%;
    z-index: 12;
    top: 0;
    left: 0;
    padding-top: 50px;

    &::before {
      content: "Дополнительно";
      font-family: @mont;
      font-size: 18px;
      line-height: 18px;
      font-weight: 700;
      margin-bottom: 66px;
      display: block;
      text-align: center;
      color: @white;
    }
  }
}

.header__bottom-wrapper {
  display: flex;
  align-items: center;
  min-height: 44px;

  @media screen and (max-width: @small) {
    flex-direction: column;
  }
}

.header__catalog-wrapper {
  position: relative;

  @media screen and (max-width: @small) {
    display: none;
  }
}

.header__catalog-item--close-btn-wrapp {
  display: none;

  @media screen and (max-width: @extra-large) {
    display: block;
    position: absolute !important;
    z-index: 12;
    top: 98px;
    right: 60px;

    .header__catalog-close {
      display: block;
      padding: 0;
      border: 0;
      position: absolute;
      width: 30px;
      height: 30px;
      background-color: transparent;

      &::before {
        content: "";
        width: 37px;
        height: 2px;
        display: block;
        position: absolute;
        top: 13px;
        left: -4px;
        background-color: @white;
        transform: rotate(45deg);
      }

      &::after {
        content: "";
        width: 37px;
        height: 2px;
        display: block;
        position: absolute;
        top: 13px;
        right: -4px;
        background-color: @white;
        transform: rotate(-45deg);
      }
    }
  }

  @media screen and (max-width: @small) {
    display: block;
    position: absolute !important;
    z-index: 12;
    top: 46px;
    right: 22px;
    width: 25px;
    height: 25px;
  }
}

.header__catalog-list {
  position: absolute;
  padding-top: 35px;
  background-color: #F4F7FB;
  min-height: 515px;

  @media screen and (max-width: @extra-large) {
    display: none;
    width: 100%;
    height: 100%;
    position: fixed;
    background-color: #13274B;
    top: 0;
    left: 0;
    z-index: 11;
    padding-top: 98px;

    @media screen and (max-width: @small) {
      padding-top: 50px;
      padding-left: 20px;
      padding-right: 20px;
    }

    &::before {
      content: "Каталог товаров";
      font-family: @mont;
      font-size: 30px;
      line-height: 30px;
      font-weight: 700;
      margin-bottom: 55px;
      display: block;
      text-align: center;
      color: @white;
    }
  }

  @media screen and (max-width: @small) {
    display: none;
    width: 100%;
    height: 100%;
    position: fixed;
    background-color: #13274b;
    top: 0;
    left: 0;
    z-index: 11;
    padding-top: 55px;

    &::before {
      content: "Каталог товаров";
      font-family: @mont;
      font-size: 18px;
      line-height: 18px;
      font-weight: 700;
      margin-bottom: 66px;
      display: block;
      text-align: center;
      color: #fff;
    }
  }
}


.header__nav-list {
  @media screen and (max-width: @small) {
    position: absolute;
    padding-top: 35px;
    background-color: #F4F7FB;
    min-height: 515px;
  }

  @media screen and (max-width: @small) {
    display: none;
    width: 100%;
    height: 100%;
    position: fixed;
    background-color: #13274B;
    top: 0;
    left: 0;
    z-index: 11;
    padding-top: 50px;
    padding-left: 20px;
    padding-right: 20px;

    &::before {
      content: "Дополнительно";
      font-family: @mont;
      font-size: 18px;
      line-height: 18px;
      font-weight: 700;
      margin-bottom: 66px;
      display: block;
      text-align: center;
      color: @white;

      @media screen and (max-width: @small) {
        font-size: 18px;
        line-height: 18px;
        font-weight: 700;
        margin-bottom: 66px;
      }
    }
  }
}

.header__catalog-item {
  padding-left: 43px;
  padding-right: 45px;
  margin-bottom: 24px;
  position: relative;

  &::after {
    content: "";
    display: block;
    width: 7px;
    height: 7px;
    position: absolute;
    top: 11px;
    right: 20px;
    background-image: url("../img/svg/triangle-blue.svg");
    background-size: 7px 7px;
    background-position: 50%;
    background-repeat: no-repeat;

    @media screen and (max-width: @extra-large) {
      background-image: url("../img/svg/triangle-white.svg");
    }
  }

  &:last-child {
    &::after {
      display: none;
    }
  }

  &:nth-child(1) {
    &::before {
      content: "";
      display: block;
      width: 15px;
      height: 15px;
      position: absolute;
      top: 7px;
      left: 20px;
      background-image: url("../img/svg/catalog-icon-1.svg");
      background-size: 15px 15px;
      background-position: 50%;
      background-repeat: no-repeat;
    }
  }

  &:nth-child(2) {
