      background-size: 15px 15px;
      background-position: 50%;
      background-repeat: no-repeat;
    }
  }

  &:nth-child(4) {
    &::before {
      content: "";
      display: block;
      width: 15px;
      height: 15px;
      position: absolute;
      top: 7px;
      left: 20px;
      background-image: url("../img/svg/catalog-icon-4.svg");
      background-size: 15px 15px;
      background-position: 50%;
      background-repeat: no-repeat;
    }
  }

  &:nth-child(5) {
    &::before {
      content: "";
      display: block;
