      left: 20px;
      background-image: url("../img/svg/catalog-icon-7.svg");
      background-size: 15px 15px;
      background-position: 50%;
      background-repeat: no-repeat;
    }
  }

  &:nth-child(8) {
    &::before {
      content: "";
      display: block;
      width: 15px;
      height: 15px;
      position: absolute;
      top: 7px;
      left: 20px;
      background-image: url("../img/svg/catalog-icon-8.svg");
      background-size: 15px 15px;
      background-position: 50%;
      background-repeat: no-repeat;
    }
  }

  &::before {
    @media screen and (max-width: @extra-large) {
      display: none !important;
    }
  }
}

.header__catalog-link {
  color: @black;
  font-size: 14px;
  line-height: 17px;
  font-family: @mont;
  font-weight: 600;

  @media screen and (max-width: @extra-large) {
    color: @white;
  }

  &:hover {
    color: @blue;
  }
}

.header__nav {
  padding-left: 50px;

  @media screen and (max-width: @extra-large) {
    padding-left: 20px;
    margin-left: auto;
  }

  @media screen and (max-width: @small) {
    margin-left: 0;
    padding: 0;
    margin-right: auto;
  }
}

.header__nav-list {
  display: flex;
  width: 100%;

  @media screen and (max-width: @small) {
    flex-direction: column;
  }
}

.header__nav-item {
  margin-right: 85px;

  @media screen and (max-width: @extra-large) {
    margin-right: 18px;
  }

  @media screen and (max-width: @small) {
    margin-bottom: 40px;

    &:last-child {
      margin-bottom: 35px;
    }
  }

  .header__nav-link {
    color: #C3C8D2;
  }
}

.header__nav-item--close-btn-wrapp {
  display: none;

  @media screen and (max-width: @small) {
    display: block;
    position: absolute !important;
    z-index: 12;
    top: 46px;
    right: 22px;
    width: 25px;
    height: 25px;

    .header__nav-close {
      display: block;
      padding: 0;
      border: 0;
      position: absolute;
      width: 25px;
      height: 25px;
      background-color: transparent;

      &::before {
        content: "";
        width: 27px;
        height: 2px;
        display: block;
        position: absolute;
        top: 13px;
        left: 2px;
        background-color: @white;
        transform: rotate(45deg);
      }

      &::after {
        content: "";
        width: 27px;
        height: 2px;
        display: block;
        position: absolute;
        top: 13px;
        right: -4px;
        background-color: @white;
        transform: rotate(-45deg);
      }
    }
  }
}

.header__catalog-close {
  @media screen and (max-width: @small) {
    width: 25px !important;
    height: 25px !important;
  }

  &::before {
    @media screen and (max-width: @small) {
      width: 29px !important;
      left: 0 !important;
    }
  }

  &::after {
    @media screen and (max-width: @small) {
      width: 29px !important;
    }
  }
}

.header__nav-item--active {
  .header__nav-link {
    color: @white;
  }
}

.header__nav-link {
  font-size: 14px;
  line-height: 17px;
  font-family: @mont;
  font-weight: 600;

  @media screen and (max-width: @small) {
    font-size: 13px;
  }
}

.header__rev-tel {
  margin-left: auto;

  @media screen and (max-width: @small) {
    width: 100%;
    margin-left: 0;
    margin-right: auto;
    border-top: 1px solid #52688E;
    padding-top: 30px;
  }
}

.header__rev-tel-btn {
  color: @white;
  font-size: 14px;
  line-height: 17px;
  font-family: @mont;
  font-weight: 600;
  padding-left: 30px;
  background-color: transparent;
  border: none;
  position: relative;

  &::after {
    content: "";
    display: block;
    width: 19px;
    height: 19px;
    position: absolute;
    top: 0;
    left: 0;
    background-image: url("../img/svg/tel-icon.svg");
    background-size: 19px 19px;
    background-position: 0 0;
    background-repeat: no-repeat;
  }
}

@media screen and (max-width: @small) {
  .header__catalog-list--show-mobile {
    display: block !important;
  }
}

.header--catalog {
  .header__catalog-list {
    display: none;
    z-index: 5;
  }
}
